.payment {
	padding-bottom: 50px;

	//payment__title
	&__title {
		margin-bottom: 20px;
		color: #000;
		font-size: 24px;
		@include r(480) {
			font-size: 20px; } }

	//payment__text
	&__text {
		color: #000;
		font-size: 16px;
		@include r(480) {
			font-size: 15px; } }
	//payment__items

	//payment__item
	&__item {
		margin-bottom: $gutter/2;
		@extend %box; }

	//payment__icons
	&__icons {
		margin-top: $gutter/2;
		@extend %flexCenter;
		@include r(480) {
			flex-wrap: wrap; }
		&--robokassa {
			width: 17%;
			margin-left: -20px;
			margin-right: $gutter*2;
			@include r(768) {
				width: 20%; }
			@include r(480) {
				width: 30%;
				margin-bottom: $gutter/2; } }
		&--pay-method {
			width: 50%;
			@include r(768) {
				width: 70%; }
			@include r(480) {
				width: 100%; } } } }

//payment
