@mixin cs-scroll {
	@include scroll;
	@include scrollbar {
		width: 4px; }
	@include scrollbar-track {
		width: 4px; }
	@include scrollbar-thumb {
		background-color: #281241; } }

@mixin bdrs {
	border-radius: $border-radius; }

@mixin bd {
	border: 1px solid $color-border--lighten;
	border-radius: $border-radius; }

%flex {
	display: flex; }

%flexCenter {
	display: flex;
	align-items: center; }

%flexColumn {
	display: flex;
	flex-direction: column; }

%textLight {
	font-family: $f-light;
	font-weight: 300; }

%inputStyle {
	display: inline-block;
	padding: 8px 30px 8px 15px;
	background-color: #fff;
	@include placeholder {
		color: $gray--light;
		font-size: 14px; } }

%responsMobile {
	@include r(480) {
		max-width: 425px;
		width: 100%; }
	@include r(425) {
		max-width: 414px; }
	@include r(414) {
		max-width: 375px; }
	@include r(375) {
		max-width: 355px; }
	@include r(320) {
		max-width: 300px; } }

%shadow {
	box-shadow: 0 2px 4px 0 rgba(0,0,0,.1), 0 0 4px 0 rgba(0,0,0,.1); }

%box {
	padding: 20px 20px 25px;
	border: 1px solid $gray--lighten;
	border-radius: $border-radius; }
