.button {
	display: inline-block;
	width: 230px;
	height: 40px;
	line-height: 38px;
	font-family: $f-light;
	font-size: 14px;
	text-align: center;
	text-decoration: none;
	border: 0;
	border-radius: $border-radius;
	position: relative;
	@include tr(.35s);

	&:hover {
		box-shadow: $bxs-hover;
		color: #fff; }

	//button--accent
	&--accent {
		color: #fff;
		background-color: $color-accent;
		&:hover {
			background-color: lighten($color-accent, 10%); } }

	//button--gray
	&--gray {
		color: #000;
		background-color: #D5D1D1;
		&:hover {
			color: #000; } }

	//button--transparent
	&--transparent {
		color: #000;
		border: 1px solid $color-border--light;
		background-color: transparent;
		&:hover {
			color: #000;
			border: 1px solid #e0e0e0;
			background-color: #e0e0e0;
			box-shadow: 0 3px 3px 0 rgba(0,0,0,0.05), 0 1px 7px 0 rgba(0,0,0,0.1), 0 3px 1px -1px rgba(0,0,0,0.05); } }

	//button--back
	&--back {
		&::before {
			content: '\f177';
			font-family: fontawesome;
			@include vy;
			left: 20px; } }

	//button--filter
	&--filter {
		width: 55px;
		height: 55px;
		line-height: 55px;
		text-align: center;
		padding: 0;
		background-color: $color-accent;
		border-radius: 50%;
		box-shadow: 0 2px 5px 0 rgba(0,0,0,.16), 0 2px 10px 0 rgba(0,0,0,.12);
		outline: none;
		position: fixed;
		bottom: 10px;
		right: 10px;
		z-index: z(fixed);
		@include rmin(769) {
			display: none; }
		.icon {
			fill: #fff;
			width: 25px;
			height: 25px;
			@include vcenter; } }

	//button-link
	&-link {
		font-size: 14px;
		color: $color-accent;
		border-left: 0;
		border-right: 0;
		border-top: 0;
		border-bottom: 1px dashed $color-accent;
		background-color: transparent;
		outline: none;
		position: relative;
		&:hover {
			border-bottom: 1px dashed lighten($color-accent, 10%); }

		&--exit {
			padding-left: 20px;
			&::before {
				content: '';
				display: block;
				@include s(logout);
				position: absolute;
				top: 4px;
				left: 0; }
			span {
				border-bottom: 1px dashed $color-accent; }
			&:hover {
				border-bottom: none;
				span {
					border-bottom: none; } } } } }

//button

.back-to-search {
	&::before {
		content: '\f002';
		font-family: fontawesome;
		@include vy;
		left: -20px; }
	&:hover {
		&::before {
			color: lighten($color-accent, 10%); } } }
//back-to-search
