.form {

	&.is-success {
		.success {
			@include trfY(0); } }

	//form__input
	&__input {
		margin-bottom: 25px;
		&:last-child {
			margin-bottom: 0; } }

	//form__box
	&__box {
		margin-bottom: 25px;
		@extend %flexCenter;
		.password-recovery {
			margin-left: auto;
			font-family: $f-light;
			font-size: 14px;
			color: $gray--light;
			&:hover {
				text-decoration: underline; } } }

	//form__btn
	&__btn {
		width: 100%; } }
//form

.success {
	text-align: center;
	background-color: #fff;
	@include coverdiv;
	@include trfY(-200%);
	@include tr;
	z-index: (z-success);
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;

	//success__text
	&__text {
		margin: auto;
		text-transform: uppercase;
		font-family: $f-bold;
		font-size: 23px;
		color: #000;
		&--small {
			text-transform: initial;
			font-family: $f;
			font-size: 15px; } } }
//success

.cs-input {
	width: 100%;
	margin-bottom: 20px;
	position: relative;

	&.is-invalid {
		.cs-input {
			&__field {
				border: 1px solid $color-error; }
			&__error {
				display: block; } } }

	//cs-input__title
	&__title {
		color: $gray;
		font-size: 15px; }

	//cs-input__field
	&__field {
		width: 100%;
		height: 100%;
		// padding: 0 15px
		font-size: 15px;
		border: 1px solid $gray--light;
		border-radius: $border-radius;
		@extend %inputStyle;
		&:focus {
			border-color: $color-border; } }

	//cs-input__error
	&__error {
		display: none;
		white-space: nowrap;
		font-family: $f-medium;
		font-size: 13px;
		line-height: 1;
		color: $color-error;
		position: absolute;
		bottom: -15px;
		left: 0; }

	//cs-input--select
	&--select {
		width: 100%;
		height: 100%;
		line-height: 26px;
		&.is-invalid {
			border: 0;
			.select2-container .select2-selection--single {
				border: 1px solid $color-error; } } }

	//cs-input--rating
	&--rating {
		.cs-input__title {
			display: block;
			margin-bottom: 5px; } } }
//cs-input
