.nav {
	//nav__list
	&__list {
		@extend %flexCenter; }

	//nav__item
	&__item {
		margin-right: 15px;
		position: relative;
		&::before {
			content: '';
			display: block;
			width: 1px;
			height: 27px;
			background-color: rgba($gray, .9);
			@include vy;
			right: -7.5px; }
		& + & {}
		&:last-child {
			margin-right: 0;
			&::before {
				display: none; } }
		&.is-active {
			.nav__link {
				&::after {
					width: 100%; } } }
		&.cs-dropdown {
			border-radius: $border-radius $border-radius 0 0;
			&:hover {
				.nav__link {
					color: #000;
					background-color: #fff;
					&::before {
						content: '\f106'; } } }
			.nav__link {
				padding: 5px 25px 5px 10px;
				line-height: 1.7;
				transition: initial;
				&::after {
					display: none; }
				&::before {
					content: '\f107';
					font-family: fontawesome;
					display: inline-block;
					@include vy;
					right: 10px; } } }

		.cs-dropdown {
			&__link {
				&::after {
					@include rmin(481) {
						display: none; } } } } }
	//nav__link
	&__link {
		display: block;
		padding: 0 10px;
		font-size: 14px;
		line-height: 100px;
		border-radius: $border-radius;
		color: #fff;
		position: relative;
		&::after {
			content: '';
			display: block;
			height: 4px;
			width: 100%;
			background-color: $color-accent;
			position: absolute;
			bottom: 0;
			left: 0;
			width: 0;
			@include tr; }
		&:hover {
			color: #fff;
			&::after {
				width: 100%; } } }
	//nav--main
	&--main {
		@include r(992) {
			display: none; }
		.nav {
			&__item {
				&--auth {
					display: none;
					min-width: 170px;
					width: 100%;
					.nav__link {
						@include text-overflow; } } } } } }
//nav

.mobile-nav {
	display: none;
	padding-top: 70px;
	padding-bottom: 70px;
	background-color: #fff;
	cursor: pointer;
	position: fixed;
	top: -70px;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 199;
	transform: translateY(70px);
	overflow-y: auto;
	@include smooth-scroll;

	//mobile-menu__list
	&__list {
		margin: 0 30px 15px 50px;
		padding-bottom: 15px;
		border-bottom: 1px solid $color-border--lighten; }

	//mobile-menu__item
	&__item {
		&--auth {
			display: none; } }

	//mobile-menu__link
	&__link {
		display: block;
		padding: 10px 0 10px 30px;
		font-size: 14px;
		color: $color-secondary;
		position: relative;
		&:hover, &:focus {
			color: #000; }
		&::after {
			content: '\f105';
			font-family: fontawesome;
			font-size: 18px;
			@include vy;
			right: 0; }
		&::before {
			content: '';
			display: inline-block;
			@include vy;
			left: 0; }
		@each $m in catalog, company, contacts, delivery, garanty, home, lk, news, partners, pay, contacts {
			&--#{$m} {
				&::before {
					@include s(mobile__#{$m}); } } }
		&--lk {
			&::after {
				display: none; } }

		&--back {
			&::after {
				content: '\f104';
				right: auto;
				left: 0; } }

		&--not-auth {}

		&--auth {
			display: none; } }

	//mobile-menu__search
	&__search {
		position: fixed;
		right: 22px;
		top: 21px;
		z-index: z(dropdown); }

	//mobile-nav--sub
	&--sub {
		padding-top: 70px;
		padding-left: 50px;
		padding-right: 30px;
		background-color: #fff;
		@include coverdiv;
		z-index: z(fixed);
		transform: translateX(200%);
		transition: transform 0.35s linear;
		will-change: transform;
		&.is-open {
			transform: translateX(0); } }

	.search {
		&__submit {
			@include s(search--mobile); }
		&__field {
			color: #000; } }

	.search-hint {
		border: 1px solid #bbb;
		border-top-color: transparent;
		background-color: #fff;
		&__item {
			& + .search-hint__item {
				border-top-color: #bbb; } }
		&__link {
			color: $color-text; } }

	.cs-dropdown {
		&.is-active {
			&::before {
				content: '\f107'; } }
		&::before {
			content: '\f105';
			right: 0;
			top: 3px;
			transform: translateY(0); }
		&__list {
			position: static;
			border-top: 0;
			box-shadow: none; }
		&__link {
			padding-left: 25px; } }

	.phone {
		margin-top: 30px;
		margin-bottom: 20px;
		@extend %flexCenter;
		&__link {
			margin: auto;
			color: $color-text; } }

	.social {
		&__list {
			margin: auto; } } }
//mobile-nav

.menu-toggle {
	@include vy;
	left: 10px;
	z-index: 201;
	@include rmin(993) {
		display: none; }

	&.is-open {
		.menu-toggle {
			&__boxes {
				transform: rotate(45deg);
				transition-delay: 0.4s; } }
		.menu-toggle {
			&__box {
				background-color: $color-text;
				&:first-child {
					transform: scaleX(0);
					transition: transform 0.2s cubic-bezier(0.694,  0.048, 0.335, 1.000); }
				&:nth-child(3) {
					width: 0;
					transition: width 0.2s cubic-bezier(0.694,  0.048, 0.335, 1.000); }
				&--other {
					height: 30px;
					transition: height 0.2s cubic-bezier(0.694,  0.048, 0.335, 1.000);
					transition-delay: 0.3s;
					background-color: $color-text; } } } }

	//menu-toggle__boxes
	&__boxes {
		width: 30px;
		height: 18px;
		position: relative;
		transition: transform 0.3s cubic-bezier(0.694,  0.048, 0.335, 1.000); }

	//menu-toggle__box
	&__box {
		display: block;
		width: 100%;
		height: 2px;
		background-color: #fff;
		margin-top: 7px;
		&:first-child {
			margin-top: 0;
			transform: scaleX(1);
			transition: transform 0.2s cubic-bezier(0.694,  0.048, 0.335, 1.000);
			transition-delay: 0.4s; }
		&:nth-child(3) {
			width: 65%;
			transition: width 0.2s cubic-bezier(0.694,  0.048, 0.335, 1.000);
			transition-delay: 0.4s; }
		//menu-toggle__box--other
		&--other {
			width: 2px;
			height: 0;
			background-color: #fff;
			position: absolute;
			top: -5px;
			left: 50%;
			margin-left: -1px;
			transition: height 0.4s cubic-bezier(0.694,  0.048, 0.335, 1.000);
			z-index: 10; } } }
//menu-toggle
