.header {
	height: 100px;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: z(fixed);
	@include tr;
	@include r(992) {
		height: 70px; }

	&.is-fixed {
		height: 70px;
		.header__inner {
			height: 70px; }
		.logo {
			&__img {
				width: 60px;
				height: 50px; } }
		.nav {
			&__link {
				line-height: 70px; } } }

	//header__inner
	&__inner {
		height: 100px;
		@include tr;
		@extend %flex;
		@include r(992) {
			height: 70px; } }

	//header__left
	&__left, &__right {
		width: 50%;
		@extend %flexCenter; }

	&__left {
		@include r(1024) {
			width: 60%; } }

	//header__right
	&__right {
		justify-content: flex-end;
		@include r(1024) {
			width: 40%; } }

	&__logo, &__cart, &__search {
		margin-right: 30px; }

	//header__search
	&__search {
		@include r(992) {
			display: none; } }

	//header__phone
	&__phone {
		@include r(1024) {
			display: none; } }

	//header__logo
	&__logo {
		@include r(992) {
			margin-right: 0;
			@include vcenter; } }

	//header__cart-link
	&__cart-link {
		margin-right: 40px;
		@include r(992) {
			margin-right: 15px; } } }
//header

.logo {
	display: inline-block;
	&__img {
		width: 80px;
		height: 70px;
		@include tr;
		@include r(992) {
			width: 60px;
			height: 50px; } } }
//logo

.search {
	max-width: 300px;
	width: 100%;
	display: inline-block;
	position: relative;
	height: 32px;
	padding: 0;
	position: relative;

	&__field {
		height: 32px;
		padding: 3px;
		padding-right: 45px;
		display: inline-block;
		font-family: $f-light;
		font-size: 15px;
		color: #fff;
		width: 0px;
		border: none;
		outline: none;
		cursor: pointer;
		background: none;
		position: absolute;
		top: 0;
		right: 0;
		z-index: 3;
		transition: width .4s cubic-bezier(0.000, 0.795, 0.000, 1.000);
		@include placeholder {
			color: #fff; }

		&:focus:hover {
			border-bottom: 1px solid #BBB; }

		&:focus {
			width: 300px;
			z-index: 1;
			border-bottom: 1px solid #BBB;
			cursor: text;
			@include r(320) {
				width: 250px; } } }

	&__submit {
		display: inline-block;
		height: 30px;
		width: 26px;
		text-indent: -10000px;
		cursor: pointer;
		border: none;
		background-color: transparent;
		@include s('search');
		position: absolute;
		top: 2px;
		right: 0;
		z-index: 2; }

	&__icon {
		fill: #fff; } }
//search

.cart-link {
	cursor: pointer;
	position: relative;

	&:hover {
		.cart__icon {
			color: $gray--light; } }

	//cart__icon
	&__icon {
		fill: #fff; }

	//cart__badge
	&__badge {
		position: absolute;
		top: -11px;
		left: 10px; } }
//cart-link

.phone {
	position: relative;

	//phone__link
	&__link {
		display: block;
		color: #fff;
		font-size: 20px;
		line-height: 1; }

	//phone__text
	&__text {
		color: #fff;
		font-family: $f-light;
		font-size: 11px;
		line-height: 1;
		letter-spacing: 1.43px;
		text-transform: uppercase; }

	//phone--icon
	&--icon {
		.phone__link {
			padding-left: 25px;
			position: relative;
			&::before {
				content: '';
				display: block;
				@include s(telephone);
				position: absolute;
				top: 3px;
				left: 0; } } } }
//phone



.search-hint {
	min-height: 143px;
	max-height: 100%;
	width: 100%;
	border: 0;
	border-radius: 0 0 $border-radius $border-radius;
	background-color: #281241;
	position: absolute;
	top: 100%;
	left: 0;
	right: 0;
	z-index: z(dropdown);
	overflow-y: auto;
	@include cs-scroll;

	@include r(480) {
		border-right-color: transparent;
		border-left-color: transparent {
	    box-shadow: 0 10px 20px rgba(#000, .2);
	    top: 100%; } }

	//search-hint__item
	&__item {
		& + & {
			border-top: 1px solid #3d1e61; }

		&.is-selected {
			background-color: #eee; } }


	//search-hint__link
	&__link {
		padding: 7px 10px;
		@extend %flexCenter;
		transition: initial;
		display: flex;
		color: #bdbdbd;
		font-size: 14px;
		&:hover {
			color: #bdbdbd;
			background-color: #3d1e61; } }
	//search-hint__bade
	&__bade {
		width: 98px;
		margin-right: 5px;
		padding: 1px 5px;
		font-family: $f-medium;
		text-align: center;
		text-transform: uppercase;
		font-size: 10px;
		color: #000;
		border-radius: $border-radius; } }
//search-hint
