.cs-accordeon {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.cs-accordeon__item.is-open .cs-accordeon__icon::before {
  content: '';
  transform: rotate(90deg);
}

.cs-accordeon__item.is-open .cs-accordeon__icon::after {
  transform: rotate(90deg);
  opacity: 0;
}

.cs-accordeon__icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: #707070;
  border-radius: 50%;
  position: absolute;
  top: 1px;
  right: 0;
}

.cs-accordeon__icon::after, .cs-accordeon__icon::before {
  content: '';
  display: block;
  position: absolute;
  background-color: #fff;
  transform-origin: 50% 50%;
  will-change: transform;
  -webkit-transition: all 0.35s ease;
  -moz-transition: all 0.35s ease;
  -ms-transition: all 0.35s ease;
  -o-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.cs-accordeon__icon::after {
  top: 9.5px;
  right: 4.5px;
  width: 11px;
  height: 1px;
}

.cs-accordeon__icon::before {
  top: 4.5px;
  right: 9.5px;
  width: 1px;
  height: 11px;
}

.cs-accordeon__content {
  width: 100%;
}

.cs-accordeon__title {
  font-family: roboto-300, sans-serif;
  font-size: 15px;
  color: #000;
  cursor: pointer;
  position: relative;
}

.cs-dropdown {
