}

.cs-dropdown__list {
  display: none;
  color: #000;
  background-color: #fff;
  border-top: 1px solid #e3dede;
  border-radius: 0 0 2px 2px;
  position: absolute;
  top: 99%;
  left: 0;
  width: 100%;
}

.cs-dropdown__link {
  display: block;
  padding: 5px 10px 6px;
  color: #000;
  font-size: 14px;
  transition: initial;
  position: relative;
}

.cs-dropdown__link:hover {
  color: #000;
  background-color: #e3dede;
}

.cs-dropdown__link::after {
  content: '\f105';
  font-family: fontawesome;
  font-size: 18px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
}

