.error {
	//error__wrap
	&__wrap {
		padding: 100px 0 50px;
		@extend %flexCenter;
		@include i8-l {
			padding-top: 35px; }
		@include r(480) {
			padding: 35px 0; } }
	//error__content
	&__content {
		max-width: 860px;
		margin: auto;
		text-align: center; }
	//error__title
	&__title {
		margin-bottom: 50px;
		font-family: $f-light;
		font-size: 200px;
		background: -webkit-linear-gradient(#af3186, #632483);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		@include r(1024) {
			font-size: 150px; }
		@include r(768) {
			font-size: 120px; } }

	//error__text
	&__text {
		font-size: 24px;
		line-height: 36px;
		@include r(1024) {
			font-size: 20px;
			line-height: 30px; }
		@include r(768) {
			font-size: 18px;
			line-height: 25px; } } }
