.breadcrumbs {
	padding: $gutter 0;
	@include r(768) {
		padding: 15px 0; }

	//breadcrumbs__wrap
	&__wrap {
		@extend %flexCenter;
		.button-link--exit {
			margin-left: auto; }
		@include r(480) {
			display: none; } }

	//breadcrumbs__list
	&__list {
		@extend %flexCenter; }

	//breadcrumbs__item
	&__item {
		position: relative;
		& + & {
			margin-left: 15px; }
		&::after {
			content: '/';
			font-size: 14px;
			@include vy;
			right: -10px; }
		&:last-child {
			@include r(480) {
				max-width: 335px;
				@include text-overflow; }
			@include r(375) {
				max-width: 285px;
				@include text-overflow; }
			@include r(320) {
				max-width: 235px;
				@include text-overflow; }
			&::after {
				display: none; } } }

	//breadcrumbs__link
	&__link {
		font-size: 14px;
		.breadcrumbs__title {
			color: $color-accent; } }

	//breadcrumbs__title
	&__title {
		font-size: 14px;
		color: #000;
		@include r(320) {
			font-size: 13px; } }

	//breadcrumbs--icon
	&--icon {
		.breadcrumbs {
			&__list {
				@extend %flexCenter;
				justify-content: spase-between;
				@include r(480) {
					justify-content: center; } }
			&__item {
				margin: auto;
				&.is-active, &.is-checked {
					.breadcrumbs__link {
						color: #000; }
					.icon {
						fill: #000; } }
				&:last-child {
					.breadcrumbs__link {
						&::before {
							display: none; } } }
				&::after {
					display: none; }
				@include i8-l {
					&:not(.is-active) {
						display: none; }
					.breadcrumbs__link {
						font-size: 16px; } }
				@include r(480) {
					&:not(.is-active) {
						display: none; }
					.breadcrumbs__link {
						font-size: 16px; } } }
			&__link {
				padding: 0 35px 0 45px;
				color: $color-text;
				font-size: 18px;
				line-height: 1;
				position: relative;
				@include r(768) {
					padding: 0 22px 0 30px;
					font-size: 15px; }
				&::before {
					content: '\f105';
					font-family: fontawesome;
					font-size: 18px;
					@include vy;
					right: 0;
					@include r(768) {
						display: none; } }
				.icon {
					fill: $gray--light;
					@include vy;
					left: 0;
					&-cart {
						width: 21px; }
					&-user {
						width: 20px; }
					&-delivery--transparent {
						width: 27px; }
					&-pay {
						width: 23px; }
					&-check {
						width: 18px; } } } } } }
//breadcrumbs
