//base style
html {
	height: 100%;
	overflow-x: hidden; }

body {
	position: relative;
	min-width: 320px;
	font-family: $font-family-base;
	font-size: $font-size-base;
	color: $color-text;
	line-height: $line-height-base;
	background-color: #fff;
	overflow-x: hidden;
	z-index: 1;
	scroll-behavior: smooth;
	@include smooth-scroll;
	@include smooth-font;
	@include cs-scroll; }

header,footer,main,div,section,ul,ol {
	@include clr; }

b, strong {
	font-family: $f-bold; }

input, textarea, select {
	outline: none;
	@include placeholder {
		font-family: $f-light;
		font-size: 14px;
		color: $color-placeholder; } }

textarea, select {
	min-height: 100px;
	width: 100%;
	border: 1px solid $gray--light;
	border-radius: $border-radius;
	resize: none;
	@include cs-scroll; }

img {
	display: block;
	width: 100%; }

a {
	color: $color-accent;
	text-decoration: none;
	outline: none;
	@include tr;
	&:hover {
		color: lighten($color-accent, 10%); } }

h1,.h1, h2,.h2, h3,.h3, h4,.h4, h5,.h5, h6,.h6 {
	margin-top: 0;
	font-family: $f;
	color: #000; }

h1,.h1 {
	font-family: $f-bold;
	font-weight: 600;
	font-size: 36px;
	line-height: 1.05em;
	@include r(1024) {
		font-size: 34px;
		line-height: 1.125em; }
	@include r(768) {
		font-size: 28px; } }

h2,.h2 {
	font-family: $f-bold;
	font-weight: 600;
	font-size: 36px;
	line-height: 1.25em;
	@include r(1024) {
		font-size: 32px;
		line-height: 1.25em; }
	@include r(768) {
		font-size: 25px;
		line-height: 1.15384615em; } }

h3,.h3 {
	font-size: 28px;
	font-family: $f-medium;
	font-weight: 500;
	line-height: 1.25em;
	@include r(1024) {
		font-size: 24px; }
	@include r(768) {
		font-size: 22px;
		line-height: 1.13636364em; } }

h4,.h4, h5,.h5, h6,.h6 {
	font-size: 18px;
	font-weight: 400;
	line-height: 1.22222222em;
	margin-bottom: $half-indent;
	@include r(768) {
		line-height: 1.11111111em; } }

p {
	margin-bottom: 20px;
	color: #000;
	font-size: 15px;
	line-height: 1.7; }

//HELPER CLASSES
.is-hidden {
	display: none !important; }
.hidden {
	display: none; }
.lg-only, .md-only, .sm-only, .xs-only {
	display: none; }
@include r(1200) {
	.lg-only {
		display: block; }
	.lg-hide {
		display: none; } }
@include r(1024) {
	.md-only {
		display: block; }
	.md-hide {
		display: none; } }
@include r(768) {
	.sm-only {
		isplay: block; }
	.sm-hide {
		display: none; } }
@include r(480) {
	.xs-only {
		display: block; }
	.xs-hide {
		display: none !important; } }

.row {
	@include row-flex(); }

.col-2 {
	@include col();
	@include size(6);
	@include size-sm(12); }
