.cart {

	//cart__wrap
	&__wrap {
		padding-bottom: 50px; }

	//cart__title
	&__title {
		margin-bottom: 40px;
		font-family: $f-medium;
		font-weight: 500;
		font-size: 25px;
		@include r(480) {
			text-align: center; } }

	//cart__inner
	&__inner {
		margin-bottom: 60px;
		border: 1px solid $gray--lighten;
		@extend %flex;
		@include r(1024) {
			flex-wrap: wrap;
			border: 0; } }

	//cart__left
	&__left {
		width: calc(100% - 300px);
		@include r(1024) {
			width: 100%;
			margin-bottom: $gutter; } }

	//cart__right
	&__right {
		width: 300px;
		margin-left: auto;
		@include r(1024) {
			width: 100%;
			border: 0;
			display: flex;
			justify-content: center;
			align-items: center; } }

	//cart__items
	&__items {
		@extend %flexColumn; }

	//cart__sum
	&__sum {
		width: 300px;
		padding: 5px 25px 11px;
		@include r(1024) {
			width: 100%;
			text-align: center;
			.button-box {
				max-width: 480px;
				margin: auto;
				display: flex;
				align-items: center; } }
		@include r(480) {
			padding: 5px 0 11px;
			.button-box {
				max-width: 100%;
				flex-direction: column; } }
		&-title {
			display: block;
			margin-bottom: 10px;
			font-family: $f-bold;
			font-size: 25px;
			color: #000;
			@include r(1024) {
				margin-bottom: $gutter; }
			.fa {
				font-size: 22px; } }
		.button {
			margin-bottom: 10px;
			&:last-child {
				margin-bottom: 0; }
			@include r(1024) {
				margin-bottom: 0;
				margin-right: 15px;
				&:last-child {
					margin-right: 0; } }
			@include r(480) {
				margin-right: 0;
				margin-bottom: 10px;
				&:last-child {
					margin-bottom: 0; } } } }

	//cartbutton-box
	&__button-box {
		text-align: right;
		@include r(480) {
			text-align: center; } }

	//cart__textarea
	&__textarea {
		width: 100%;
		max-height: 200px;
		min-height: 150px;
		margin-bottom: $gutter;
		padding: 10px 15px;
		@include bd;
		resize: none;
		@include cs-scroll; }

	.button {
		@include r(480) {
			width: 100%; }
		&:first-child {
			margin-right: 15px;
			@include r(480) {
				margin-bottom: 15px;
				margin-right: 0; } } }

	.product-item {
		&:last-child {
			border-bottom-color: transparent;
			@include r(1024) {
				border-bottom-color: $gray--lighten; } }
		@include r(480) {
			&__desc {
				display: flex;
				align-items: center;
				flex-wrap: wrap; }
			&__title {
				width: 100%; }
			&__price, &__counter {
				margin-bottom: 0; }
			&__counter {
				margin-left: auto; } }
		&--wide {
			padding: 20px 50px 20px 20px;
			@include r(768) {
				padding: 20px; } } }

	.row {
		margin-bottom: 25px; } }
//cart

.cart-tab {

	&__list {
		margin: $gutter 0;
		@extend %flexCenter;
		justify-content: spase-between;
		@include r(480) {
			justify-content: center; } }

	&__item {
		margin: auto;
		outline: none;
		&.ui-state-active, &.ui-state-hover {
			.cart-tab__link {
				color: #000; }
			.icon {
				fill: #000; } }
		&:last-child {
			.cart-tab__link {
				&::before {
					display: none; } } }
		&::after {
			display: none; }
		@include i8-l {
			.cart-tab__link {
				font-size: 16px; } }
		@include r(480) {
			.cart-tab {
				&__text {
					display: none; }
				&__link {
					font-size: 16px;
					padding-right: 2px; } } } }

	&__link {
		padding: 0 35px 0 45px;
		color: $color-text;
		font-size: 18px;
		line-height: 1;
		position: relative;
		outline: none;
		@include r(768) {
			padding: 0 22px 0 30px;
			font-size: 15px; }
		&::before {
			content: '\f105';
			font-family: fontawesome;
			font-size: 18px;
			@include vy;
			right: 0;
			@include r(768) {
				display: none; } }
		.icon {
			fill: $gray--light;
			@include vy;
			left: 0;
			&-cart {
				width: 21px; }
			&-user {
				width: 20px; }
			&-delivery--transparent {
				width: 27px; }
			&-pay {
				width: 23px; }
			&-check {
				width: 18px; } } } }
//.cart-tab

.pedit {

	//pedit__wrap
	&__wrap {
		@extend %flexCenter; }

	//pedit__item
	&__item {
		margin-bottom: 25px;
		position: relative;
		@extend %flexCenter;
		flex-wrap: wrap;
		&--text {
			align-items: flex-start;
			.pedit__title {
				text-align: right;
				@include r(768) {
					text-align: left; } } }
		.cs-input {
			margin-bottom: 0; } }

	//pedit__icon
	&__icon {
		color: $gray;
		@include vy;
		right: 10px;
		cursor: pointer; }

	//pedit__title
	&__title {
		min-width: 185px;
		margin-right: 30px;
		font-size: 14px;
		@include r(480) {
			width: 100%;
			margin-bottom: 15px; } }

	//pedit__field
	&__field {
		flex: 1; }

	//pedit__date
	&__date {
		@extend %flexCenter;
		.pedit__title {
			min-width: 260px;
			margin-right: 60px; }
		.pedit__item {
			margin-right: 15px;
			&:last-child {
				margin-right: 0; } } }

	//pedit__select
	&__select {}

	//pedit__checkbox
	&__checkbox {
		& + & {
			margin-left: 30px; }
		.cs-checkbox__title {
			font-size: 14px; } }

	//pedit__checkbox
	&__textarea {
		min-height: 132px;
		width: 100%;
		border-radius: $border-radius;
		border-color: $gray--light;
		resize: none;
		&:focus {
			border-color: $color-border; } }

	//pedit__btn
	&__btn {
		width: auto;
		padding: 0 45px;
		@include r(480) {
			width: 100%; } }

	//pedit__file
	&__file {
		margin-left: auto;
		padding-left: 25px;
		@extend %textLight;
		font-size: 14px;
		color: $color-text;
		cursor: pointer;
		position: relative;
		@include r(480) {
			margin-left: 0; }
		&::before {
			content: '';
			display: inline-block;
			margin-right: 5px;
			@include s(clip);
			@include vy;
			left: 0; }
		span {
			margin-right: 7px;
			font-family: $f;
			font-weight: 400;
			font-size: 15px;
			color: #000;
			border-bottom: 1px dashed $color-border; }
		input {
			display: none; } } }
//pedit

.cart-success {

	//cart-success__wrap
	&__wrap {
		padding: 20px 0 50px;
		text-align: center;
		@include r(480) {
			padding-top: 20px; } }

	//cart-success__title
	&__title {
		@extend .cart__title;
		display: inline-block;
		position: relative;
		&::before {
			display: block;
			content: '';
			width: 17px;
			height: 30px;
			border: solid $color-accent;
			border-width: 0 5px 5px 0;
			position: absolute;
			left: -35px;
			top: -8px;
			transform: rotate(45deg);
			@include r(480) {
				left: 10px; } } }

	//cart-success__subtitle
	&__subtitle, &__text {
		display: block;
		margin-bottom: 40px;
		color: #000; }

	&__subtitle {
		font-family: $f-bold;
		font-size: 18px;
		&.mb-10 {
			margin-bottom: 10px; }
		span {
			font-size: 28px; } } }
//cart-success

.delivery-item {
	margin-bottom: 25px;
	display: flex;
	@include r(480) {
		margin-bottom: $gutter*2;
		text-align: center;
		align-items: center;
		flex-direction: column; }

	//delivery-item__checkbox
	&__checkbox {
		width: 200px;
		height: 135px;
		margin-right: 25px;
		padding: 20px;
		text-align: center;
		@include bd;
		background-color: #fff;
		position: relative;
		display: flex;
		flex-direction: column;
		@include r(480) {
			margin-bottom: 10px;
			margin-right: 0; } }

	&__img {
		width: 120px;
		margin: auto; }

	//delivery-item__text
	&__text {
		flex: 1; }

	//delivery-item__title
	&__title {
		display: block;
		margin-bottom: 5px;
		font-family: $f-medium;
		font-size: 18px;
		color: #000; }

	//delivery-item__desc
	&__desc {
		font-size: 14px;
		color: #000; }

	.cs-checkbox {
		&__box {
			border-color: $color-border--lighten;
			position: absolute;
			top: 10px;
			left: 10px; }
		&__title {
			margin: 0 auto; }
		.icon {
			width: 50px;
			height: 50px;
			margin: auto;
			fill: $gray--light; } } }
//delivery-item
