.page-home {}
//page-home

.firstscreen {
	min-height: 550px;
	margin-bottom: 35px;
	background-image: url('../img/general/firstscreen-shape.png'), linear-gradient(to right,#090113,#1c0439);
	&__title {
		margin-top: 35px;
		margin-bottom: 60px;
		// font-family: $f-light
		font-weight: 300;
		line-height: 1.3;
		text-align: center;
		color: #fff; } }
//firstscreen

.home-search {
	@include r(768) {
		margin-bottom: 30px; }

	//home-search__icon
	&__in {
		@extend %flex;
		justify-content: center;
		@include r(768) {
			flex-wrap: wrap; } }

	//home-search__icon
	&__icon {
		width: 27px;
		height: 27px;
		margin-right: 15px;
		fill: $gray--light;
		@include tr;
		@include r(768) {
			fill: $color-secondary; }
		&--series {
			width: 22px;
			height: 22px; }
		&--articul {
			width: 24px;
			height: 24px; } }

	//home-search__form
	&__form {
		@extend %flexCenter;
		@include r(480) {
			display: block; } }

	//home-search__block
	&__block {
		flex: 1;
		@extend %flexCenter;
		@include r(480) {
			margin-bottom: 15px;
			flex-direction: column; }
		.select2-container {
			width: 50% !important;
			margin-right: 15px;
			@include r(480) {
				width: 100% !important;
				margin-right: 0;
				margin-bottom: 15px;
				&:last-child {
					margin-bottom: 0; } } } }

	//home-search__btn
	&__btn {
		text-transform: uppercase;
		letter-spacing: 0.7px;
		@include r(768) {
			display: block;
			margin: auto; }
		@include r(480) {
			width: 100%; } }

	//home-search__list
	&__list {
		width: 20%;
		margin: 0 auto;
		@include r(768) {
			width: 50%; }
		@include r(480) {
			width: 100%; }
		&:first-child {
			margin-left: 0; }
		&:last-child {
			margin-right: 0; }
		&--wide {
			width: 100% !important;
			@extend %flexCenter;
			@include r(320) {
				flex-direction: column;
				align-items: flex-start; }
			.home-search__item {
				& + .home-search__item {
					margin-left: 15px;
					@include r(320) {
						margin-left: 0; } } }
			.home-search__link {
				margin-bottom: 0; } } }

	//home-search__link
	&__link {
		display: inline-block;
		margin-bottom: 10px;
		padding: 3px 8px;
		font-size: 14px;
		color: #000;
		transition: initial;
		position: relative;
		&::after {
			content: '';
			display: block;
			width: 100%;
			height: 1px;
			border-bottom: 1px dashed #000; }
		&:hover {
			color: #fff;
			background-color: $color-accent;
			border-bottom-color: transparent;
			border-radius: $border-radius;
			&::after {
				border-color: transparent; } } }

	//home-search__search
	&__search {
		height: 38px;
		line-height: 38px;
		margin-right: 15px;
		padding: 0 30px 0 15px;
		border: 1px solid $color-border;
		border-radius: $border-radius;
		position: relative;
		flex: 1;
		@include r(480) {
			width: 100%;
			margin-right: 0;
			margin-bottom: 15px; }
		input {
			width: 100%;
			color: #000;
			border: 0; } }

	//home-search__clear
	&__clear {
		width: 30px;
		font-family: $f;
		font-size: 14px;
		text-align: center;
		font-style: normal;
		cursor: pointer;
		@include vy;
		right: 0;
		&:hover {
			color: #000; } }

	//home-search .select
	.select2-container .select2-selection--single {
		height: 38px; }
	.select2-container--default .select2-selection--single .select2-selection__rendered {
		line-height: 38px;
		padding-right: 30px; }
	.select2-container--default .select2-selection--single .select2-selection__arrow {
		width: 25px;
		top: 7px; }
	.select2-dropdown {
		max-height: 100px;
		@include cs-scroll; } }
//home-search

.advert {
	@include r(768) {
		margin-bottom: $section-indent - 30px; }
	.info-block {
		&__item {
			.icon-delivery {
				width: 50px;
				margin-left: 4px;
				margin-right: 26px; } } } }
//adv

.info-block {
	//info-block__items
	&__items {
		@include row-flex();
		justify-content: space-between;
		@include r(1024) {
			justify-content: center; } }

	//info-block__item
	&__item {
		@include col();
		@include size(4);
		@include size-sm(6);
		@include size-xs(12);
		@extend %flexCenter;
		@include r(768) {
			margin-bottom: 30px; }
		.icon-delivery {
			margin-right: 26px;
			margin-left: 4px;
			width: 50px;
			@include r(768) {
				width: 70px; }
			@include r(480) {
				width: 57px;
				margin-left: 8px;
				margin-right: 28px; }
			@include r(375) {
				width: 65px;
				margin-left: 3px;
				margin-right: 25px; } } }

	//info-block__icon
	&__icon {
		width: 60px;
		height: 40px;
		margin-right: 20px;
		fill: $color-text; }

	//info-block__title
	&__title {
		color: $color-accent;
		font-size: 18px; }

	//info-block__text
	&__text {
		color: #000000;
		font-size: 14px; }

	//info-block__desc
	&__desc {
		margin-bottom: 0; } }
//info-block

.baner {
	//bener__bg
	&__bg {
		max-height: 390px;
		height: 100%;
		padding: 60px 40px;
		@include cover;
		@include r(768) {
			max-height: 280px;
			padding: 40px; }
		@include r(480) {
			max-height: 200px;
			padding: 20px; } }
	//bener__title
	&__title {
		margin-bottom: 25px;
		color: #000;
		font-family: $f-black;
		@include r(480) {
			font-size: 20px;
			margin-bottom: 10px; } }
	//bener__desc
	&__desc {
		max-width: 300px;
		margin-bottom: 30px;
		color: #000;
		font-family: $f-medium;
		font-size: 18px;
		@include r(480) {
			font-size: 14px;
			margin-bottom: 10px; } }
	//bener__phone
	&__phone {
		color: #000;
		font-family: $f-black;
		font-size: 24px;
		@include r(480) {
			font-size: 18px; } }

	//baner .slick-slide
	.slick-slide {
		margin: 0; }
	//baner .slick-dots
	.slick-dots {
		padding: 0 44px;
		bottom: 25px;
		@include r(768) {
			display: none !important; }
		li {
			button {
				&::before {
					width: 10px;
					height: 10px;
					border-radius: 50%; } } } } }
//baner

.category {
	margin-bottom: 50px;
	//category__items
	&__items {
		@extend %flex;
 }		// flex-wrap: wrap
	//category__item
	&__item {
		min-height: 190px;
		min-width: 220px;
		width: 100%;
		margin-bottom: 10px;
		margin-right: 10px;
		background-color: #eee;
		position: relative;
		overflow: hidden;
		filter: grayscale(100%);
		&:hover {
			filter: grayscale(0);
			.category__img {
				transform: scale(1.05); }
			.category__title {
				text-decoration: underline; } } }
	//category__title
	&__title {
		color: #ffffff;
		font-size: 14px;
		position: absolute;
		bottom: 20px;
		left: 15px;
		z-index: 2; }
	//category__img
	&__img {
		transform: scale(1);
		transition: transform .5s ease-in-out;
		@include coverdiv; } }
//catefoty

.cs-grid {
	@extend %flex;
	@include r(480) {
		flex-direction: column; }
	&-container {
		margin-right: -10px;
		@extend %flex;
		width: 100%;
		@include r(1024) {
			flex-direction: column; } }
	//category__box--left
	&--left {
		width: 40%;
		margin-right: 10px;
		@include r(1024) {
			width: 100%;
			margin-right: -10px;
			.cs-grid--column {
				flex-direction: row;
				flex: 1;
				.category__item {
					width: 50%;
					&:last-child {
						margin-right: 0; } } }
			.category__item {
				width: 33.33%; } }
		@include r(768) {
			.category__item {
				width: 50%; }
			.cs-grid--column {
				.category__item {
					width: 100%; } } }
		@include r(480) {
			.category__item {
				width: 100%; } } }
	//category__box--right
	&--right {
		width: 60%;
		@include r(1024) {
			width: 100%; } }
	//category__box--column
	&--column {
		flex-direction: column;
		.category__item {} }
	//catecory__box--wide
	&--wide {
		@extend %flex;
		.category__item {
			@include r(1024) {
				width: 50%;
				&:last-child {
					margin-right: 0; } }
			&:first-child {
				flex: 1;
				@include r(1024) {
					flex: inherit; } }
			@include r(480) {
				width: 100%; } } }
	//catecory__box--reverse
	&--reverse {
		.category__item {
			&:first-child {
				flex: initial; }
			&:last-child {
				flex: 1; } } } }
//category
